.UserCorrectionMenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.UserCorrectionMenu__FlaggedIngredient {
  color: red;
}

.UserCorrectionMenu__UnflaggedIngredient {
  color: grey;
}

.UserCorrectionMenu__Textarea,
.UserCorrectionMenu__ReadOnlyOverlay {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 8vh;
  line-height: 8vh;
  font-family: sans-serif;
  display: block;
  width: 100vw;
  white-space: pre-line;
  border: 0;
  padding: 0;
  margin: 0;
}

.UserCorrectionMenu__Textarea {
  z-index: 1;
  background-color: transparent;
  color: transparent;
  caret-color: black;
  outline: none;
}



