.UploadImageMenu__Logo {
  height: 40vh;
  display: block;
  margin-top: 5vh;
  margin-bottom: 10vh;
  margin-left: auto;
  margin-right: auto;
}

.UploadImageMenu {
  position: fixed;
  top: 0;
  background-color: var(--dark-green);
  width: 100vw;
  height: 100vh;
  text-align: center;
}

.UploadContainer input {
  display: none;
}

.UploadContainer {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #eee;
  color: var(--dark-green);
  font-size: 6vh;
  padding: 3vh;
  border-radius: 1.5vh;
  display: inline-block;
  min-width: 60vw;
  -webkit-box-shadow: 0 0.8vh 2vh 0.2vh #2226;
          box-shadow: 0 0.8vh 2vh 0.2vh #2226;
  overflow: hidden;
}

/* Inspiration: https://codepen.io/sebj54/pen/oxluI */

.UploadContainer::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0;
  padding-top: 0;
  border-radius: 100%;
  background-color: #57912F80;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.UploadContainer:active::before {
  width: 120%;
  padding-top: 120%;
  -webkit-transition: width 0.2s ease-out, padding-top .2s ease-out;
  -o-transition: width 0.2s ease-out, padding-top .2s ease-out;
  transition: width 0.2s ease-out, padding-top .2s ease-out;
}



